#table {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

#table td, #table th {
  border: 1px solid rgb(184, 45, 45);
  padding: 8px;
}

#table tr:nth-child(even){background-color: #bba800;}

#table tr:hover {background-color: rgb(0, 0, 0);}

#table th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
  background-color: #aa1704;
  color: rgb(165, 16, 16);
}
